* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-style: normal;
}

.header {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.header__logo-link {
  width: 40px;
  height: 40px;
  display: block;
}

.header__menu {
  display: flex;
}

.header__menu-link {
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  display: flex;
  position: relative;
}

.header__menu-link--is-active {
  color: #00acdc;
}

