  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header__menu-item {
  color: #060b35;
}

.header__menu-item:not(:last-child) {
  margin-right: 20px;
}

.header__menu-item:hover .header__menu-link {
  color: #00acdc;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  height: 406px;
  padding: 32px 16px 16px;
  font-family: Roboto;
  transition-duration: .3s;
}

.card:hover {
  color: #34568b;
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
